import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAlignLeft } from '@fortawesome/free-solid-svg-icons';
import { Navbar, Button, NavbarToggler, Collapse, Nav, NavItem, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';

export default props => {
  const [isOpen, setOpen] = useState(true)
  const toggle = () => setOpen(!isOpen)
  return (
    <Navbar color="light" light className="shadow-sm p-3 mb-5 bg-white rounded" expand="md">
      <Button className="btn-toggle" color="primary" onClick={props.toggle}>
        <FontAwesomeIcon icon={faAlignLeft} />
      </Button>
      <NavbarToggler onClick={toggle} />
      <Collapse isOpen={isOpen} navbar>
        <Nav className="ml-auto" navbar>
          <NavItem id="profile-bar">
            <img className="rounded-circle" src={require('../../../assets/images/pp.png')} height="35px" alt="User" />
          </NavItem>
          <UncontrolledDropdown inNavbar>
            <DropdownToggle nav caret>
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem disabled>
                Arfani Hidayat
                </DropdownItem>
              <DropdownItem divider />
              <DropdownItem>
                <i className="fa fa-sign-out"></i> Log out
                </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Nav>
      </Collapse>
    </Navbar>
  );
}