import React, { useState } from 'react';
import { Collapse, NavItem, NavLink } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';

const SubMenu = props => {
  const [collapsed, setCollapsed] = useState(true)
  const toggleNavbar = () => setCollapsed(!collapsed)
  const { icon, title, items } = props;

  return (
    <>
      <NavItem onClick={toggleNavbar} className={!collapsed ? 'menu-open' : ''}>
        <NavLink className={collapsed ? "dd-right" : "dropdown-toggle"}>
          <FontAwesomeIcon icon={icon} className="mr-2" />{title}
        </NavLink>
      </NavItem>
      <Collapse isOpen={!collapsed} navbar className={'items-menu ' + (!collapsed ? 'mb-1' : '')}>
        {items.map((item, index) => (
          <NavItem
            active={'/admin/' + props.branch + "/" + item.target === props.location.pathname ? true : false}
            key={index}
            className="pl-4"
          >
            <NavLink tag={Link} to={'/admin/' + props.branch + "/" + item.target}>
              <FontAwesomeIcon icon={faArrowRight} /> {item.title}
            </NavLink>
          </NavItem>
        ))}
      </Collapse>
    </>
  );
}

export default SubMenu;