import React, { useState } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
export default props => {
    const { first, middle, last } = props.rootData.name;
    const [firstName, setFirstName] = useState(first);
    const [middleName, setMiddleName] = useState(middle);
    const [lastName, setLastName] = useState(last);
    const [show, setShow] = useState(false);
    const [text, setText] = useState('');
    const {profession} = props.rootData.lang;
    const {github, facebook, wordpress, linkedin} = props.rootData.links;
    const [githubLink, setGithubLink] = useState(github);
    const [facebookLink, setFacebookLink] = useState(facebook);
    const [wordpressLink, setWordpressLink] = useState(wordpress);
    const [linkedInLink, setLinkedInLink] = useState(linkedin);

    const updateHomeData = () => {
        const updateName = fetch('https://restapi-cv.herokuapp.com/main/name'
            , {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json;charset=utf-8'
                },
                body: JSON.stringify({ firstName, middleName, lastName })
            }
        );

        updateName.then(r => r.json())
            .then(r => {
                const { first, middle, last } = r['updated data to'];
                setText(`Your name is updated to ${first} ${middle} ${last}`);
                setShow(true);
            })
            .catch(r => console.log(r))
    }

    const PopUp = () =>
        <Modal centered={true} isOpen={show}>
            <ModalHeader toggle={() => setShow(!show)}>SUCCESS !</ModalHeader>
            <ModalBody>
                {text}
            </ModalBody>
            <ModalFooter>
                <Button color="primary" onClick={() => setShow(!show)}>Ok</Button>
            </ModalFooter>
        </Modal>

    const propListStyle = {
        background: 'gray',
        borderRadius: '5px',
        marginRight: '5px',
        padding: '4px',
        paddingRight: '0',
        color: '#fff',
    }
    const profList = profession.map((element, i) => <span key={i} style={propListStyle}><span>{element} </span><span><button className="btn btn-danger btn-sm" value={element} onClick={evt => alert(evt.target.value)}>x</button></span></span>)

    return (
        <>
            <h2 className="mb-4">Home Page Data</h2>
            <label htmlFor="first-name">Name : </label>
            <div className="form-row">
                <div className="col-md mb-1">
                    <input
                        id="first-name"
                        type="text"
                        className="form-control"
                        value={firstName}
                        onChange={evt => setFirstName(evt.target.value)}
                    />
                </div>
                <div className="col-md mb-1">
                    <input
                        type="text"
                        className="form-control"
                        value={middleName}
                        onChange={evt => setMiddleName(evt.target.value)}
                    />
                </div>
                <div className="col-md mb-2">
                    <input
                        type="text"
                        className="form-control"
                        value={lastName}
                        onChange={evt => setLastName(evt.target.value)}
                    />
                </div>

            </div>
            <div className="form-group">
                <label htmlFor="profession">Profession :</label>
                <input className="form-control mb-2" type="text" id="profession" placeholder="Press enter to add one" />
                <div id="professionList">
                    {profList}
                </div>
            </div>
            <div className="form-group">
                <label htmlFor="linkGithub">Links : </label>
                <div className="form-row">
                    <div className="col-md-6 mb-1">
                        <input title="Github link" type="text" className='form-control' id="githubLink" placeholder="Github link" defaultValue={githubLink} onChange={evt => setGithubLink(evt.target.value)} />
                    </div>
                    <div className="col-md-6 mb-1">
                        <input title="Facebook link" type="text" className='form-control' id="facebookLink" placeholder="Facebook link" defaultValue={facebookLink} onChange={evt => setFacebookLink(evt.target.value)} />
                    </div>
                    <div className="col-md-6 mb-1">
                        <input title="Website link" type="text" className='form-control' id="websiteLink" placeholder="Website link" defaultValue={wordpressLink} onChange={evt => setWordpressLink(evt.target.value)} />
                    </div>
                    <div className="col-md-6">
                        <input title="LinkedIn link" type="text" className='form-control' id="linkedInLink" placeholder="LinkedIn link" defaultValue={linkedInLink} onChange={evt => setLinkedInLink(evt.target.value)} />
                    </div>
                </div>
            </div>
            <div className="form-group">
                <label htmlFor="wa">Number WA : </label>
                <input title="WA Number" type="text" className="form-control" id="wa" placeholder="WhatsApp Number" />
            </div>
            <div className="form-group">
                <label htmlFor='whoAreYou'>Who are you ? : </label>
                <textarea className="form-control" rows="6" placeholder="Describe yourself !" id="whoAreYou"></textarea>
            </div>

            <label htmlFor="detail">Details : </label>
            <div className="form-row">
                <div className="col-md-6 mb-1">
                    <input type="date" id="birth" placeholder="Birth" className="form-control" title="Birth" defaultValue="1993-05-03" />
                </div>
                <div className="col-md-6 mb-1">
                    <input type="text" id="residence" placeholder="Residence" className="form-control" title="Residence" />
                </div>
                <div className="col-md-6 mb-1">
                    <input type="email" id="email" placeholder="Email" className="form-control" title="Email" />
                </div>
                <div className="col-md-6 mb-1">
                    <select id="freelance" className="form-control" title="Freelance">
                        <option value="true">Available</option>
                        <option value="false">Not Available</option>
                    </select>
                </div>
                <div className="col-md-12 mb-3">
                    <textarea id="address" rows="3" placeholder="Address" className="form-control" title="Address"></textarea>
                </div>
            </div>

            <PopUp />

            <input
                type="button"
                className="form-control btn btn-primary"
                value="UPDATE"
                onClick={updateHomeData}
            />
        </>
    )
}
