import React, { Fragment } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import Dashboard from './Dashboard';
import Login from "./Login";

export default function Admin(props) {
    // setting background of admin pages
    document.body.style.background = null;
    return (
        <Fragment>
            <Switch>
                <Route path="/admin" component={(p) => <Dashboard rootData={props.rootData} {...p} />} />
                {/* <Route path="/admin" component={(p) => <Redirect to="/login" />} /> */}
                <Route exact path="/login" render={(p) => <Login rootData={props.rootData} {...p} />} />
                <Route render={() => <h1 className="bg-dark text-center text-light p-4">Missing admin route!!</h1>} />
            </Switch>
        </Fragment>
    )
}