import React, { useState } from 'react';

import SideBar from './adminLayout/Sidebar';
import Content from './adminLayout/Content';
import { BrowserRouter as Router, Route } from 'react-router-dom';

export default props => {
    const [isOpen, setOpen] = useState(true)
    const toggle = () => setOpen(!isOpen)

    return (
        <div id="admin-page">
            <Router>
                <Route render={
                    ({ location }) => {
                        return (
                            <>
                                <SideBar toggle={toggle} isOpen={isOpen} {...props} location={location} />
                                <Content toggle={toggle} isOpen={isOpen} {...props} location={location} />
                            </>
                        )
                    }
                }
                />
            </Router>
        </div>
    )
}