import React from 'react';
import { Container } from 'reactstrap';
import Header from './Header';
import { Switch, Route } from 'react-router-dom';
import { State } from "../";
import Home from '../pages/Home';

export default props =>
  <Container fluid className={'content ' + (props.isOpen ? 'is-open' : '')}>
    <Header toggle={props.toggle} />
    <div className="body">
      <Switch>
        <Route exact path={props.match.path} component={State} />
        <Route exact path={props.match.path + "/data/home"} component={() => <Home {...props} />} />
        <Route render={() => <h1 className="bg-dark text-center text-light p-4">Missing admin content route!!</h1>} />
      </Switch>
    </div>
  </Container>